import { Drawer, Form, Input, Button } from 'antd';
import { useEffect } from 'react';
export default function Drawers({ onClose, open, setTableData, setOpen, editData }) {
    useEffect(() => {
        if (editData !== 0) {
            editKey = editData.key
            myForm.setFieldsValue({ name: editData.name })
            myForm.setFieldsValue({ age: editData.age })
            myForm.setFieldsValue({ address: editData.address })
        }else(
            ClearForm()
        )
    }, [editData])
    let editKey = ''
    const onFinish = (values) => {
        if (editData === 0) {
            let tableItem = localStorage.getItem('TableItem')
            if (tableItem !== null&&tableItem!==0) {
                let t = JSON.parse(tableItem)
                t.push(values)
                for (let i = 0; i < t.length; i++) {
                    t[i].key = i + 1
                }
                localStorage.setItem('TableItem', JSON.stringify(t))
                setTableData(t)
            } else {
                const newValues = {
                    ...values,
                    key: 1
                }
                localStorage.setItem('TableItem', JSON.stringify([newValues]))
                setTableData([newValues])
            }
            ClearForm()
            setOpen(false)
        } else {
            let newValues = {
                ...values,
                key: editKey
            }
            let dbData = JSON.parse(localStorage.getItem('TableItem'))
            let repeatKey = ''
            dbData.map((n, m) => {
                if (editKey === n.key) {
                    repeatKey = m
                    return false
                }
            })
            dbData.splice(repeatKey,1,newValues)
            localStorage.setItem('TableItem',JSON.stringify(dbData))
            setOpen(false)
            setTableData(dbData)
        }

    }

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const [myForm] = Form.useForm()
    const ClearForm = () => {
        myForm.setFieldsValue({ name: '' })
        myForm.setFieldsValue({ age: '' })
        myForm.setFieldsValue({ address: '' })
    }


    return (
        <>
            <Drawer title={editData ? '修改信息' : "添加名单"} placement="right" onClose={onClose} open={open}>
                <Form
                    name="basic"
                    form={myForm}
                    style={{
                        maxWidth: 600,
                    }}
                    onFinish={onFinish}
                    onFinishFailed={onFinishFailed}
                    autoComplete="off"
                >
                    <Form.Item
                        label="姓名"
                        name="name"
                        rules={[
                            {
                                required: true,
                                message: '请输入您的姓名',
                            },
                        ]}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item
                        label="年龄"
                        name="age"
                        rules={[
                            {
                                required: true,
                                message: '请输入您的年龄',
                            },
                        ]}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item
                        label="住址"
                        name="address"
                        rules={[
                            {
                                required: true,
                                message: '请输入您的住址',
                            },
                        ]}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item
                        wrapperCol={{
                            offset: 8,
                            span: 16,
                        }}
                    >
                        <Button type="primary" htmlType="submit">
                            {editData ? '确认修改' : '确认添加'}
                        </Button>
                    </Form.Item>
                </Form>
            </Drawer>
        </>
    )
}