import React, { useState } from 'react';
import { Space, Table, Button, Input, } from 'antd';
import { useEffect } from 'react';
import Drawers from './Drawer';

const { Search } = Input;

const ListTable = () => {

  useEffect(() => {
    let tD = localStorage.getItem('TableItem')

    if (tD !== null) {
      let t = JSON.parse(tD)
      for (let i = 0; i < t.length; i++) {
        t[i].key = i + 1
      }
      setTableData(t)
    }
  }, [])


  const columns = [
    {
      title: '序号',
      dataIndex: 'key',
      key: 'key',
    },
    {
      title: '姓名',
      dataIndex: 'name',
      key: 'name',
      render: (text) => <a>{text}</a>,
    },
    {
      title: '年龄',
      dataIndex: 'age',
      key: 'age',
    },
    {
      title: '住址',
      dataIndex: 'address',
      key: 'address',
    },
    {
      title: '操作',
      key: 'action',
      render: (teb) => (
        <Space size="middle">
          <Button danger onClick={() => Delete(teb)}>删除</Button>
          <Button onClick={() => Edit(teb)}>修改</Button>
        </Space>
      ),
    },
  ];

  const [open, setOpen] = useState(false);
  const [editData, setEditData] = useState(0)
  const [tableData, setTableData] = useState([])


  const showDrawer = () => {
    setOpen(true)
    setEditData(0)
  };

  const onClose = () => {
    setOpen(false);
  };

  const onSearch = (value) => {
    let dbData = JSON.parse(localStorage.getItem('TableItem'))
    let result = dbData.filter((item) => {
      return Object.values(item).some((property) =>
        property.toString().toLowerCase().includes(value.toLowerCase())
      );
    });
    setTableData(result);
  }

  const Delete = (values) => {
    let temp = JSON.parse(localStorage.getItem('TableItem'))
    let newData = temp.filter((item) => (
      item.key !== values.key
    ))
    for (let i = 0; i < newData.length; i++) {
      newData[i].key = i + 1
    }
    localStorage.setItem('TableItem', JSON.stringify(newData))
    setTableData(newData)
  }

  const Edit = (values) => {
    setOpen(true)
    setEditData(values)
  }

  return (
    <>
      <div style={{ margin: '20px auto 20px 10px' }}>
        <Button type="primary" onClick={() => showDrawer()} style={{ margin: '0 20px auto' }}>增加名单</Button>
        <Search
          placeholder="进行模糊搜索"
          onSearch={onSearch}
          style={{
            width: 200,
          }}
        />
      </div>
      <Table columns={columns} dataSource={tableData} />
      <Drawers onClose={onClose} open={open} setTableData={setTableData} setOpen={setOpen} editData={editData} />

    </>
  )
}
export default ListTable;


